/**
 * Í≤ΩÎß§ Î™©Î°ù Ï°∞Ìöå API
 * GET /api/auctions
 *
 * Î™®Îì† Í≤ΩÎß§ Îß§Î¨ºÏùÑ Ï°∞ÌöåÌïòÏó¨ JSONÏúºÎ°ú Î∞òÌôòÌï©ÎãàÎã§.
 */
export async function GET(request: NextRequest) {
  try {
    console.log("üîç Í≤ΩÎß§ Î™©Î°ù Ï°∞Ìöå ÏöîÏ≤≠ ÏãúÏûë");

    // URL ÌååÎùºÎØ∏ÌÑ∞ÏóêÏÑú ÌéòÏù¥ÏßÄ Ï†ïÎ≥¥ Ï∂îÏ∂ú
    const { searchParams } = new URL(request.url);
    const page = parseInt(searchParams.get("page") || "1");
    const limit = parseInt(searchParams.get("limit") || "6");

    console.log(`üìÑ ÌéòÏù¥ÏßÄ: ${page}, Ï†úÌïú: ${limit}`);

    // Ï†ÑÏ≤¥ Îß§Î¨º Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ± (Ï¥ù 20Í∞ú)
    const totalItems = 20;
    const auctionItems = [];

    // Í∞Å Îß§Î¨ºÎßàÎã§ Í≥†Ïú†Ìïú ÏãúÎìú ÏÉùÏÑ± (Îçî Í∞ÑÎã®Ìïú ÌòïÌÉú)
    for (let i = 0; i < totalItems; i++) {
      const uniqueSeed = `${Math.random().toString(36).substr(2, 9)}`;
      auctionItems.push(generateRandomPropertyWithSeed(uniqueSeed));
    }

    // ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Í≥ÑÏÇ∞
    const startIndex = (page - 1) * limit;
    const endIndex = startIndex + limit;
    const paginatedItems = auctionItems.slice(startIndex, endIndex);

    const totalPages = Math.ceil(totalItems / limit);
    const hasNextPage = page < totalPages;
    const hasPrevPage = page > 1;

    console.log(
      `‚úÖ ÌéòÏù¥ÏßÄ ${page}/${totalPages}: ${paginatedItems.length}Í∞úÏùò Îß§Î¨ºÏùÑ Î∞òÌôòÌñàÏäµÎãàÎã§`
    );

    // ÏÑ±Í≥µ ÏùëÎãµ Î∞òÌôò
    return NextResponse.json(
      {
        success: true,
        data: paginatedItems,
        pagination: {
          currentPage: page,
          totalPages,
          totalItems,
          itemsPerPage: limit,
          hasNextPage,
          hasPrevPage,
        },
        message: "Í≤ΩÎß§ Î™©Î°ùÏùÑ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ï°∞ÌöåÌñàÏäµÎãàÎã§",
      },
      { status: 200 }
    );
  } catch (error) {
    console.error("‚ùå Í≤ΩÎß§ Î™©Î°ù Ï°∞Ìöå Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);

    // Ïò§Î•ò ÏùëÎãµ Î∞òÌôò
    return NextResponse.json(
      {
        success: false,
        error: "Í≤ΩÎß§ Î™©Î°ùÏùÑ Ï°∞ÌöåÌïòÎäî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§",
        details: error instanceof Error ? error.message : "Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò",
      },
      { status: 500 }
    );
  }
}
